<template>
  <el-row :gutter="10" class="commit-review" v-cloak>
    <div v-if="!msgs.length" class="no-msg">
      {{ msgTip }}
    </div>
    <el-col v-else class="chat-record">
      <el-tabs v-model="activeName" class="health-file">
        <el-tab-pane label="聊天记录" name="msg">
          <msg-card :msg-list="msgs" :show-admin="false" />
        </el-tab-pane>
        <el-tab-pane label="健康档案" name="health">
          <health-file :baby-id="babyId" :get-info="false" />
        </el-tab-pane>
      </el-tabs>
    </el-col>
    <el-col class="review-info">
      <div class="rate-info">
        <p class="rate-item">
          <span class="rate-text">收集病史：</span>
          <el-rate
            v-model="rate.medicalHistory"
            allow-half
            :disabled="routeName === 'SeeReviews' || isSubmit"
            @change="getScore"
          />
        </p>
        <p class="rate-item">
          <span class="rate-text">病因分析：</span>
          <el-rate
            v-model="rate.etiologyAnalysis"
            allow-half
            :disabled="routeName === 'SeeReviews' || isSubmit"
            @change="getScore"
          />
        </p>
        <p class="rate-item">
          <span class="rate-text">诊断明确性：</span>
          <el-rate
            v-model="rate.diagnosticClarity"
            allow-half
            :disabled="routeName === 'SeeReviews' || isSubmit"
            @change="getScore"
          />
        </p>
        <p class="rate-item">
          <span class="rate-text">治疗方案：</span>
          <el-rate
            v-model="rate.treatmentPrograms"
            allow-half
            :disabled="routeName === 'SeeReviews' || isSubmit"
            @change="getScore"
          />
        </p>
        <p class="rate-item">
          <span class="rate-text">同理心：</span>
          <el-rate
            v-model="rate.empathy"
            allow-half
            :disabled="routeName === 'SeeReviews' || isSubmit"
            @change="getScore"
          />
        </p>
      </div>
      <el-divider />
      <p class="overall-ratings">
        综合得分：<span class="score">{{ score }}</span>
      </p>
      <el-form label-position="left" label-width="90px" :model="reviewInfo">
        <el-form-item label="点评：">
          <el-input
            v-if="character === 'doctor' && !reviewContent"
            v-model="reviewInfo.content"
            type="textarea"
            placeholder="请输入点评内容"
            maxlength="500"
            resize="none"
            show-word-limit
            @blur="setErrorText"
          />
          <div v-if="reviewContent" class="review-content">
            {{ reviewContent }}
          </div>
          <span class="error">{{ errorReviewText }}</span>
        </el-form-item>
        <el-form-item v-if="feedback" label="问题反馈：">
          <div class="review-content">{{ feedback }}</div>
        </el-form-item>
        <el-form-item
          v-if="
            type === 'human_resource' &&
              !handlerContent &&
              routeName === 'SeeReviews' &&
              feedback
          "
          label="处理结果："
        >
          <el-input
            v-model="reviewInfo.processResult"
            type="textarea"
            placeholder="请输入处理结果"
            maxlength="200"
            resize="none"
            show-word-limit
            class="process-result"
            :disabled="submitResultDisabled"
          />
        </el-form-item>
        <el-form-item
          v-if="handlerContent && routeName === 'SeeReviews'"
          label="处理结果："
        >
          <div class="review-content">{{ handlerContent }}</div>
        </el-form-item>
        <el-form-item
          v-if="character === 'doctor' && $route.name === 'CommitReview'"
          label=""
        >
          <p class="commit-btn">
            <el-button v-if="!isSubmit" type="primary" @click="commitReview"
              >提交</el-button
            >
            <el-button v-if="isSubmit" type="primary" @click="nextComment"
              >点评下一个</el-button
            >
            <el-button
              :type="isSubmit ? 'info' : 'warning'"
              :disabled="isSubmit"
              @click="switchMsg"
              >跳过</el-button
            >
          </p>
        </el-form-item>
        <el-form-item
          v-if="
            !handlerContent &&
              routeName === 'SeeReviews' &&
              type === 'human_resource' &&
              feedback
          "
          label=""
        >
          <el-button
            type="primary"
            style="padding: 6px 20px;height: 32px;"
            :disabled="submitResultDisabled"
            @click="submitResults = true"
            >提交</el-button
          >
        </el-form-item>
        <el-form-item
          v-if="
            !feedback &&
              routeName === 'SeeReviews' &&
              character === 'hm' &&
              isFeedbackSuccess
          "
          label=""
        >
          <p class="sign">
            如果对本次点评有异议，请及时进行
            <span class="feedback" @click="openFeedback">
              反馈
              <svg class="icon feedback-icon" aria-hidden="true">
                <use xlink:href="#iconquanju_fankui" />
              </svg>
            </span>
          </p>
        </el-form-item>
      </el-form>
    </el-col>
    <tip
      v-if="feedbackShow"
      :tip-show.sync="feedbackShow"
      btn-text="确定"
      class="center"
      :show-cancel-btn="true"
      :has-footer="true"
      :close-hid="true"
      width="568px"
      title="点评问题反馈"
      @btnHandle="btnFeedbackShow"
      @cancel="
        feedbackShow = false
        feedbackContent = ''
      "
    >
      <p slot="content">
        <el-input
          v-model="feedbackContent"
          type="textarea"
          placeholder="请描述您遇到的问题，200字以内"
          maxlength="200"
          resize="none"
          show-word-limit
          class="feedback-textarea"
          @change="watchFeedback"
        />
        <span class="error">{{ errorText }}</span>
      </p>
    </tip>
    <el-dialog
      title="提示"
      :visible.sync="submitResults"
      width="400px"
      center
      top="30vh"
    >
      <p style="text-align: center;">提交后，将不能再修改,请确定是否提交？</p>
      <span slot="footer" class="dialog-footer">
        <el-button
          @click="
            submitResults = false
            feedbackContent = ''
          "
          >取 消</el-button
        >
        <el-button type="primary" @click="commitResult">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="提示"
      :visible.sync="hasReview"
      width="400px"
      center
      top="30vh"
    >
      <p style="text-align: center;">当前没有可点评的咨询记录，请稍后重试</p>
      <span slot="footer" class="dialog-footer">
        <el-button @click="hasReview = false">取 消</el-button>
        <el-button type="primary" @click="switchMsg">确 定</el-button>
      </span>
    </el-dialog>
  </el-row>
</template>

<script>
import Tip from '@/components/Dialog'
import {
  reviewMessage,
  getReviewDetail,
  serviceFeedback,
  hrReviewResult,
  goToReview
} from '@/api/learningAdmin'
import { getNowFormatDate } from '@/utils/learningMethod'
import MsgCard from '@/views/evaluate/MedicalService/components/MsgCard.vue'
import HealthFile from '@/views/medical-service/health-file/fileDetail'
export default {
  name: 'CommitReview',
  components: {
    Tip,
    MsgCard,
    HealthFile
  },
  data() {
    return {
      rate: {
        medicalHistory: 0,
        etiologyAnalysis: 0,
        diagnosticClarity: 0,
        treatmentPrograms: 0,
        empathy: 0
      },
      reviewContent: '',
      reviewInfo: {
        content: '',
        processResult: ''
      },
      feedbackShow: false,
      feedbackContent: '',
      errorText: '',
      submitResults: false,
      type: this.$store.state.user.mts_user
        ? this.$store.state.user.mts_user.type
        : '', // 用户职位角色
      character: this.$store.state.user.bus_role, // 用户角色
      routeName: this.$route.name, // 路由名称
      msgReview: {}, // 评论详情
      score: 0, // 综合分数
      feedback: '', // 反馈结果
      handlerContent: '', // 处理结果
      startTime: getNowFormatDate(new Date()),
      isSubmit: false, // 星星状态
      hasReview: false,
      errorReviewText: '',
      submitResultDisabled: false,
      isFeedbackSuccess: true,
      msgs: [],
      activeName: 'msg',
      msgTip: '',
      fd_ask: {},
      babyId: ''
    }
  },
  created() {
    this.initPage()
    console.log(this.$route.name)
  },
  methods: {
    initPage() {
      if (this.$route.params.id && this.$route.name === 'SeeReviews') {
        this.getReviewDetail()
      } else this.getMsgs()
    },
    async btnFeedbackShow() {
      if (!this.feedbackContent) {
        this.errorText = '反馈内容不能为空'
        return
      } else this.errorText = ''
      const { code, data } = await serviceFeedback({
        id: this.$route.params.id,
        feed_back: this.feedbackContent
      })
      if (code === 20000) {
        if (data.result) {
          this.$message({ message: '提交成功', type: 'success' })
          this.isFeedbackSuccess = false
          this.feedback = this.feedbackContent
        } else this.$message({ message: data.message, type: 'error' })
      }
      this.feedbackShow = false
    },
    openFeedback() {
      const id =
        this.$store.state.user &&
        this.$store.state.user.userInfo &&
        this.$store.state.user.userInfo.id
      if (this.msgReview.commentator === id) {
        this.feedbackShow = true
      } else this.$message({ type: 'warning', message: '不能反馈其他人' })
    },
    watchFeedback(val) {
      if (!val) this.errorText = '反馈内容不能为空'
      else this.errorText = ''
    },
    getScore() {
      this.score =
        this.rate.medicalHistory * 4 +
        this.rate.etiologyAnalysis * 4 +
        this.rate.diagnosticClarity * 4 +
        this.rate.treatmentPrograms * 4 +
        this.rate.empathy * 4
    },
    async commitResult() {
      const { code, data } = await hrReviewResult({
        id: this.fd_ask._id,
        handler_content: this.reviewInfo.processResult
      })
      if (code === 20000) {
        if (data.result) {
          this.$message({ message: '提交成功', type: 'success' })
          this.submitResults = false
          this.submitResultDisabled = true
        } else this.$message({ message: data.message, type: 'error' })
      }
    },
    async getReviewDetail() {
      const { code, data } = await getReviewDetail(this.$route.params.id)
      if (code === 20000 && data.result) {
        this.msgReview = data.msg_review
        this.msgs = data.msgs
        if (!data.msgs.length) {
          this.msgTip = '暂无咨询记录'
        }
        this.rate.medicalHistory = data.msg_review.medical_history / 4
        this.rate.etiologyAnalysis = data.msg_review.cause_analysis / 4
        this.rate.diagnosticClarity = data.msg_review.clarify_diagnosis / 4
        this.rate.treatmentPrograms = data.msg_review.therapeutic_plans / 4
        this.rate.empathy = data.msg_review.empathy / 4
        this.score = data.msg_review.score
        if (data.msg_review.content) this.isSubmit = true
        this.reviewContent = data.msg_review.content || '无'
        if (data.msg_review.feed_back) {
          this.feedback = data.msg_review.feed_back
        }
        if (data.msg_review.handler_content) {
          this.handlerContent = data.msg_review.handler_content
        }
      }
    },
    async commitReview() {
      // 提交评论
      if (!this.reviewInfo.content && this.score !== 100) {
        this.errorReviewText = '评论不能为空'
        return
      } else this.errorReviewText = ''
      const { code, data } = await reviewMessage({
        medical_history: this.rate.medicalHistory * 4,
        cause_analysis: this.rate.etiologyAnalysis * 4,
        clarify_diagnosis: this.rate.diagnosticClarity * 4,
        therapeutic_plans: this.rate.treatmentPrograms * 4,
        empathy: this.rate.empathy * 4,
        commentator: this.fd_ask.hm,
        msg_id: this.fd_ask._id,
        status: 'submitted',
        content: this.reviewInfo.content,
        review_started_at: this.startTime,
        review_ended_at: getNowFormatDate(new Date())
      })
      if (code === 20000) {
        if (data.result) {
          this.isSubmit = true
          this.$message({ message: '评价成功', type: 'success' })
        } else this.$message({ message: data.message, type: 'error' })
      }
    },
    async switchMsg() {
      if (this.hasReview) {
        this.msgs = []
        this.hasReview = false
        return
      }
      const { code } = await reviewMessage({
        commentator: this.fd_ask.hm,
        msg_id: this.fd_ask._id,
        status: 'reviewed',
        review_started_at: this.startTime,
        review_ended_at: getNowFormatDate(new Date())
      })
      if (code === 20000) {
        this.goToReview()
        this.isSubmit = false
        this.rate = {
          medicalHistory: 0,
          etiologyAnalysis: 0,
          diagnosticClarity: 0,
          treatmentPrograms: 0,
          empathy: 0
        }
        this.score = 0
        this.reviewInfo = {
          content: '',
          processResult: ''
        }
      }
    },
    async goToReview() {
      const { code, data } = await goToReview()
      if (code === 20000) {
        if (data.result && data.messages && data.messages.fd_ask) {
          this.$router.push(
            '/learning-admin/comment/commit-review' +
              '?timespan=' +
              new Date().getTime()
          )
          this.initPage()
        } else this.hasReview = true
      }
    },
    setErrorText() {
      if (this.reviewInfo.content) this.errorReviewText = ''
      else this.errorReviewText = '评论不能为空'
    },
    async getMsgs() {
      const { code, data } = await goToReview()
      if (code === 20000) {
        if (data.result && data.messages && data.messages.fd_ask) {
          this.msgs = data.messages.msgs
          this.fd_ask = data.messages.fd_ask
          this.babyId =
            data.messages.msgs.length && data.messages.msgs[0].family_member
        } else {
          this.msgTip = '暂无咨询记录'
        }
      }
    },
    nextComment() {
      this.goToReview()
      this.isSubmit = false
      this.rate = {
        medicalHistory: 0,
        etiologyAnalysis: 0,
        diagnosticClarity: 0,
        treatmentPrograms: 0,
        empathy: 0
      }
      this.score = 0
      this.reviewInfo = {
        content: '',
        processResult: ''
      }
    }
  }
}
</script>

<style scoped lang="scss">
[v-cloak] {
  display: none;
}
.commit-review {
  display: flex;
  height: 100%;
  .no-msg {
    padding-top: 50px;
    width: calc(100% - 500px);
    text-align: center;
    color: rgb(90, 94, 102);
    background: #f7f7f7;
    border: 1px solid #ebeef5;
  }
  .chat-record {
    width: calc(100% - 500px);
    height: 100%;
    background: #f7f7f7;
    border: 1px solid #ebeef5;
    border-radius: 4px;
    .msg-card {
      height: 100%;
    }
    /deep/.infinite-list-wrapper {
      height: 100%;
    }
  }
  .review-info {
    overflow: auto;
    margin-left: 20px;
    padding: 30px 20px !important;
    width: 480px;
    height: 100%;
    border: 1px solid #ebeef5;
    border-radius: 4px;
    .rate-info {
      .rate-item {
        margin-bottom: 20px;
        .rate-text {
          display: inline-block;
          width: 120px;
          font-size: 14px;
          color: #17233d;
        }
        /deep/.el-rate {
          display: inline-block;
          .el-rate__icon {
            font-size: 27px;
          }
        }
      }
    }
    .overall-ratings {
      font-size: 14px;
      font-weight: 400;
      color: #17233d;
      margin-bottom: 20px;
      .score {
        margin-left: 22px;
        font-size: 16px;
        color: #0085ff;
      }
    }
    .review {
      float: left;
      width: 70px;
      margin-bottom: 20px;
    }
    .review-content {
      float: left;
      margin-top: 10px;
      line-height: 20px;
    }
    /deep/.el-textarea__inner {
      margin-top: 10px;
      height: 273px;
    }
    .commit-btn {
      margin-top: 40px;
      margin-left: -60px;
      text-align: center;
      /deep/.el-button {
        padding: 6px 20px;
        height: 32px;
        & + .el-button {
          margin-left: 20px;
        }
      }
    }
  }
  .process-result {
    height: 160px;
    /deep/.el-textarea__inner {
      height: 160px;
    }
  }
  .sign {
    font-size: 14px;
    color: #999;
    .feedback {
      color: #0085ff;
      cursor: pointer;
      .feedback-icon {
        width: 15px;
        height: 15px;
        vertical-align: text-top;
      }
    }
  }
  .center {
    text-align: center;
    /deep/.el-dialog__header h4 {
      font-size: 14px;
      color: #17233d;
    }
    .error {
      display: block;
      margin-top: 5px;
      padding-left: 20px;
      text-align: left;
      color: #f56c6c;
    }
  }
  .feedback-textarea {
    margin-top: 15px;
    width: 488px;
    height: 200px;
    /deep/.el-textarea__inner {
      height: 100%;
    }
  }
  /deep/.el-button {
    padding: 6px 20px;
    height: 32px;
  }
  .error {
    color: #f56c6c;
  }
  .health-file {
    height: 100%;
    /deep/.el-tabs__content {
      overflow: auto;
      height: 95%;
    }
  }
}
</style>
